import React, { Component } from 'react';
import angry from '../images/angry.png';
import happy from '../images/happy.png';
import smiling from '../images/smiling.png';
import surprised from '../images/surprised.png';
import thinking from '../images/thinking.png';
import upset from '../images/upset.png';

export default class Buttons extends Component {
  constructor(props) {
    super(props);
    this.state = {
      display: { display: "none" }
    }
  }
  componentDidMount() {
    this.btnTimeout = setTimeout(() => {
      this.setState({ display: { opacity: "0" } });
      this.opacityTimeout = setTimeout(() => {
        this.setState({ display: { opacity: "1" } });
      }, 500)
    }, 5100);
  }

  render() {
    return (
      <div style={this.state.display} id="btn_wrapper">
        <div className="left_block">
          <div onClick={() => this.props.onClick("angry")} className="btn"><img src={angry} alt="angry kid" /></div>
          <div onClick={() => this.props.onClick('happy')} className="btn central_left"><img src={happy} alt="happy kid" /></div>
          <div onClick={() => this.props.onClick('thinking')} className="btn"><img src={thinking} alt="thinking kid" /></div>
        </div>
        <div className="right_block">
          <div onClick={() => this.props.onClick('smiling')} className="btn"><img src={smiling} alt="smiling kid" /></div>
          <div onClick={() => this.props.onClick('surprised')} className="btn central_right"><img src={surprised} alt="surprised kid" /></div>
          <div onClick={() => this.props.onClick('upset')} className="btn"><img src={upset} alt="upset kid" /></div>
        </div>
      </div>
    )
  }
}