import React, { Component } from 'react';
import robot from '../images/robot.png';
import hi from '../sounds/hello.m4a'
import learn from '../sounds/letLearn.m4a'


export default class Robot extends Component {
	constructor(props) {
		super(props);
		this.state = {
			content: '',
			quoteOpacity: { opacity: "0" }
		}

	}
	// here is handeling onload 'hello' 'lets learn' and uphearing of buttons
	componentDidMount() {
		this.helloTimeOut = setTimeout(() => {
			const hello = document.getElementById('hello');
			hello.currentTime = 0;
			hello.play();
			this.setState({ content: "Привіт", quoteOpacity: { opacity: "1" } });
		}, 400);

		this.learnTimeOut = setTimeout(() => {
			const learn = document.getElementById('letLearn');
			learn.currentTime = 0;
			learn.play();
			this.setState({ content: "Давай вивчати емоції" });
			setTimeout(() => {
				this.setState({ quoteOpacity: { opacity: "0" }, content: '' })
			}, 3300);
		}, 2300);
	}
	// heare we rerendering quote text and opacity of quore itself
	componentDidUpdate(prevProps) {
		if (prevProps.content !== this.props.content) {
			this.setState({ content: this.props.content });
		}
		if (prevProps.opacity !== this.props.opacity) {
			this.setState({ quoteOpacity: this.props.opacity });
		}
	}


	render() {
		return (
			<div id="robot_wraper">
				<div style={this.state.quoteOpacity} className="quote_wraper">
					<blockquote className="oval-thought">
						<p>{this.state.content}</p>
					</blockquote>
				</div>
				<img className="robot" src={robot} alt="robot" />
				<audio allow="autoplay" preload="auto" id="hello" src={hi} />
				<audio allow="autoplay" preload="auto" id="letLearn" src={learn} />
			</div>
		)
	}
}