import React, { Component } from 'react';
import './App.scss';
import Robot from './components/robot.component';
import Buttons from './components/emo.buttons';
import PopUpWindow from './components/popup.emo';
import StartAppButton from './components/start.app';
import { gifsArr } from './components/emo.gif.array';


export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      displayStart: { display: 'flex' },
      robot: false,
      buttons: false,
      seenPopup: false,
      currentBtn: '',
      content: '',
      quoteOpacity: { opacity: "0" },
      emoSound: '',
      isClicked: false
    }
    this.handelStratClick = this.handelStratClick.bind(this);
    this.handelEmoClick = this.handelEmoClick.bind(this);
  }
  handelStratClick() {
    this.setState({
      displayStart: { display: 'none' },
      robot: true,
      buttons: true
    })
  }

  handelEmoClick(button) {
    // popup window hendler
    setTimeout(() => {
      this.setState({ seenPopup: true });
      setTimeout(() => {
        this.setState({ seenPopup: false });
      }, 5000);
    }, 1200);

    let current, content, sound;
    // loop which assign object keys to external variables
    gifsArr.filter(el => el.id === button)
      .map(el => {
        current = el.emo;
        content = el.content;
        sound = el.sound;
        return el;
      });

    this.setState({
      currentBtn: current,
      content: content,
      quoteOpacity: { opacity: "1" },
      emoSound: sound,
      isClicked: !this.state.isClicked
    });

    // this timeout for seting opacity of quote to 0
    setTimeout(() => {
      this.setState({ quoteOpacity: { opacity: "0" } })
    }, 1200);

  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.emoSound !== this.state.emoSound || prevState.isClicked !== this.state.isClicked) {
      const emotion = document.getElementById('emo_sound');
      emotion.currentTime = 0;
      emotion.play();
    }
  }
  render() {
    return (
      <div className="App">
        <StartAppButton
          onClick={this.handelStratClick}
          display={this.state.displayStart}
          start={this.state.startButton} />
        {this.state.robot ? <Robot content={this.state.content} opacity={this.state.quoteOpacity} /> : null}
        {this.state.buttons ? <Buttons onClick={this.handelEmoClick} /> : null}
        {this.state.seenPopup ? <PopUpWindow emoGif={this.state.currentBtn} /> : null}
        {/* when emotion button pressed it will make sound of it */}
        <audio allow="autoplay" preload="auto" id="emo_sound" src={this.state.emoSound} />
      </div>
    );
  }

}


